          'pt-br': '<p>Ajude todos os trs sapos a encontrar suas vitrias-rgias usando <code>justify-content</code>. Desta vez, as vitrias-rgias tm muito espao em torno delas.</p><p>Caso voc esquea os possveis valores de uma propriedade, passe o cursor sobre a propriedade para visualiz-los. Tente passar o cursor sobre <code>justify-content</code>.</p>',
          'es': '<p>Ayuda a las tres ranas a encontrar sus hojas de lirio usando <code>justify-content</code>. En esta oportunidad, las hojas de lirio tienen mucho espacio a su alrededor.</p><p>Si no recuerdas los valores de una propiedad, puede pasar el cursor sobre el nombre de una e inmediatamente se mostrarn. Prueba pasando el cursor sobre <code>justify-content</code>.</p>',
          'fr': '<p>Aidez les trois grenouilles  trouver leurs nnuphars en utilisant seulement <code>justify-content</code>. Cette fois-ci, les nnuphars ont beaucoup d\'espace tout autour d\'eux.</p><p>Si vous oubliez les valeurs possibles pour une proprit, vous pouvez survoler le nom de cette proprit pour les voir. Essayez de survoler <code>justify-content</code>.</p>',
          'ru': '<p>      ,   <code>justify-content</code>.   ,     .</p><p>  ,     ,       ,   .     <code>justify-content</code>.</p>',
          'fa': '<p>   <code>justify-content</code>             .               .</p><p>      ,          . <code>justify-content</code>.</p>',
          'zh-cn': '<p><code>justify-content</code></p><p>CSS<code>justify-content</code></p>',
          'zh-tw': '<p><code>justify-content</code></p><p>CSS<code>justify-content</code></p>',
          'tr': '<p><code>justify-content</code> kuraln kullanarak  kurbaann da kendi nilfer yapran bulmasn salayn. Bu serfer, nilfer yapraklar etrafnda bolca bo alan var.</p><p> Olur da bir kuraln alabilecei deerleri unutursanz, o kuraln zerine farenizi gtrerek deerlerini grebilirsiniz. Farenizi <code>justify-content</code> zerine gtrmeyi deneyin.</p>',
          'it': '<p>Aiuta le tre rane a raggiungere le ninfee usando la propriera\' <code>justify-content</code>. Fai attenzione, questa volta le ninfee hanno molto spazio attorno a loro.</p><p>Se ti sei dimenticato alcuni valori per le proprieta\' CSS puoi portare il mouse sopra il nome della proprieta\' per visualizzare tutti i valori disponibili. Prova portando il cursore sopra <code>justify-content</code>.</p>',
          'ko': '<p><code>justify-content</code>         .      .</p><p>       ,     . <code>justify-content</code>    .</p>',
          'lt': '<p>Padkite visoms trims varlytms pasiekti savo lelijos lapus naudodami <code>justify-content</code>.  kart aplink lelijos lapus yra labai daug vietos.</p><p>Jei netiktai pamirote galimas ios komandos vertes, jas prisiminti galite uved ymekl ant komandos pavadinimo. Pabandykite uvesti ymekl ant <code>justify-content</code>.</p>',
          'vi': '<p>Gip tt c ba ch ch tm nhng l bng sng ca h bng cch s dng <code>justify-content</code>. Ln ny, nhng l bng sng c rt nhiu khng gian xung quanh chng. </p><p>Nu bn thy mnh ang qun i nhng gi tr cho thuc tnh ny, bn c th di chut qua t   xem chng. Hy th di chut qua t ny<code>justify-content</code>.</p>',
          'pl': '<p>Pom wszystkim abkom dotrze do ich listkw przy pomocy tylko waciwoci <code>justify-content</code>. Tym razem, listki maj duo miejsca wok.</p><p>Jeli nie pamitasz jakie wartoci przyjmuje ta waciowo moesz najecha myszk na jej nazw, aby je zobaczy. Sprbuj przesun myszk nad <code>justify-content</code>.</p>',
          'cs': '<p>Pomozte tmto tem abkm na jejich leknny vlastnost <code>justify-content</code>. Tentokrt je mezi leknny hodn msta.</p><p>Pokud zapomenete, jak hodnoty lze pro kterou vlastnost pout, najete na ni my. Zkuste najet na <code>justify-content</code>.</p>',
          'ja': '<p><code>justify-content</code></p><p><code>justify-content</code></p>',
          'hu': '<p>Segts mindhrom bknak megtallni a sajt tndrrzsjt pusztn a <code>justify-content</code> hasznlatval. Ezttal a tndrrzsk krl meglehetsen sok hely van.</p><p>Ha gy rzed, elfelejtetted a lehetsges rtkeket ehhez a tulajdonsghoz, vidd az egrkurzort a tulajdonsg neve fl, hogy lthasd ket. Prbld ki a <code>justify-content</code>-en.</p>',
          'eo': '<p>Helpu i tiujn tri ranojn trovi iliajn nimfeofoliojn nur pere de <code>justify-content</code>. ifoje, la nimfeofolioj havas multe da spaco irka ili.</p><p>Se vi forgesas la eblan valoron por trajton, vi povas vebi sur la nomon de la trajto por afii ilin. Provu vebi sur <code>justify-content</code>.</p>',
          'sv': '<p>Hjlp alla tre grodorna tillbaka till sina blombald genom att anvnda <code>justify-content</code>. Denna gngen har blombladen mycket utrymme runt omkring varandra.</p><p>Om du glmmer bort de accepterade vrdena fr en attribut kan du fra musen ver dem fr att se dem. Testa att fra musen ver <code>justify-content</code>.</p>',
          'uk': '<p>       ,  <code>justify-content</code>.        .</p><p>     ,        .    <code>justify-content</code>.</p>',
          'hi': '<p>   <code>justify-content</code>                     </p><p>                      <code>justify-content</code>       </p>',
          'sr': '<p>          <code>justify-content</code>.        .</p><p>  /    ,          .      <code>justify-content</code>.</p>',
          'nl': '<p>Help alle drie kikkers naar hun lelieblaadjes door gebruik te maken van <code>justify-content</code>. Deze keer is er veel plaats rond de lelieblaadjes.</p><p>Indien je de mogelijke waarden vergeten bent kun je altijd je cursor over de naam van de eigenschap houden. Probeer je cursor over <code>justify-content</code> te houden.</p>',
          'ta': '<p><code>justify-content</code> ,     .       .</p><p>      ,    hover    .  <code>justify-content</code>  hover  .</p>',
