          'vi': '<p>By gi nhng l bng sng  tri dt vo b, to thm khng gian gia chng. S dng <code>justify-content</code>. Ln ny, nhng l bng sng c khong cch bng nhau gia chng.</p>',
          'pl': '<p>Tym razem skrajne listki odpyny do kracw kontenera, zwikszajc odstpy. Uzyj <code>justify-content</code>. Tym razem listki maj t sam odlego midzy sob.</p>',
          'cs': '<p>Te leknny na stranch odpluly ke behu, zvtly se mezery. Pouijte <code>justify-content</code>. Tentokrt jsou mezi leknny rovnomrn rozestupy.</p>',
          'ja': '<p><code>justify-content</code></p>',
