          'en': '<p>Sometimes reversing the row or column order of a container is not enough. In these cases, we can apply the <code>order</code> property to individual items. By default, items have a value of 0, but we can use this property to set it to a positive or negative integer value.</p><p>Use the <code>order</code> property to reorder the frogs according to their lilypads.</p>',
          'ro': '<p>Cteodat inversarea ordinii rndului sau coloanei nu este de ajuns. n aceste cazuri, putem aplica proprietatea <code>order</code> individual elementelor. n mod implicit, elementele au valoarea 0, dar putem folosi aceast proprietate s o setm la o valoare intreag pozitiv sau negativ.</p><p>Folosete proprietatea <code>order</code> pentru a ordona broatele n funcie de nuferii lor.</p>',
          'bg': '<p>            .        <code>order</code>   .  ,    0,      ,    ,      .</p><p>  <code>order</code>,       .</p>',
          'de': '<p>Manchmal reicht es nicht aus, die horizontale oder vertikale Ausrichtung eines Containers umzukehren. In so einem Fall k&ouml;nnen wir die <code>order</code>-Eigenschaft f&uuml;r einzelne Elemente verwenden. Standardm&auml;&szlig;ig haben alle Element den Wert 0, aber wir k&ouml;nnen die Eigenschaft verwenden, um ihn auf eine positive oder negative ganze Zahl zu setzen.</p><p>Benutze die <code>order</code>-Eigenschaft, um die Fr&ouml;schen entsprechend ihrer Seerosenbl&auml;tter anzuordnen.</p>',
          'pt-br': '<p>s vezes, reverter a ordem de uma coluna ou de um container no  suficiente. Nesses casos, podemos aplicar a propriedade <code>order</code> para itens individuais. Por padro, itens tem um valor de 0, mas ns podemos usar essa propriedade para alterar para um valor inteiro positivo ou negativo.</p><p>Use a propriedade <code>order</code> para reordenar os sapos de acordo com suas vitrias-rgias.</p>',
          'es': '<p>A veces, invertir el orden de una fila o columna de un contenedor no es suficiente. En esos casos, nosotros podemos aplicar la propiedad <code>order</code> para elementos individuales. Por defecto, los elementos tienen un valor 0, pero nosotros podemos usar esta propiedad para establecerlo a un nmero entero positivo o negativo.</p><p>Usa la propiedad <code>order</code> para reordenar las ranas de acuerdo a sus hojas de lirio.</p>',
          'fr': '<p>Parfois, inverser l\'ordre de la range ou la colonne ne suffit pas. Dans ces cas, on peut appliquer la proprit <code>order</code>  des lments individuels. Par dfaut, les lments ont une valeur de 0, mais on peut utiliser cette proprit pour changer la valeur  un entier positif ou ngatif.</p><p>Utilisez la proprit <code>order</code> pour rorganiser les grenouilles par rapport  leurs nnuphars.</p>',
