          'it': '<p>A volte invertire l\'ordine di visualizzazione delle righe o delle colonne non e\' sufficiente. In questi casi possiamo applicare la proprieta\' <code>order</code> sui singoli elementi. Ogni elemento ha assegnato il valore 0 di default ma e\' possibile utilizzare questa proprieta\' per assegnare un valore intero positivo o negativo.</p><p>Utilizza <code>order</code> per muovere le rane sulla loro ninfea.</p>',
          'ko': '<p>  row column      .   <code>order</code>      . order   0,     .</p><p><code>order</code>        .</p>',
          'lt': '<p>Kartais sukeisti supanio elemento eilutes su stulpeliais vietomis negana. Tokiais atvejais galima pritaikyti komand <code>order</code> pavieniams elementams. Pagal nutyljim element eils tvark nusakanti vert yra 0, bet galima naudoti i komand, kad vert prilygintume teigiamam arba neigiamam skaiiui.</p><p>Panaudokite komand <code>order</code>, kad apkeistumte varlytes vietomis, pagal j lelij lapus.</p>',
          'vi': '<p>i khi o chiu ca hp cha theo hng ngang hay hng dc l khng . Trong nhng trng hp ny, chng ta c th p dng thuc tnh <code>order</code> cho tng hng mc ring l. Theo mc nh, cc hng mc c gi tr l 0, nhng chng ta c th thay i thuc tnh  vi mt gi tr s nguyn dng hoc m khc.</p><p>S dng thuc tnh <code>order</code>  sp xp li nhng ch ch theo cc l bng sng ca chng.</p>',
          'pl': '<p>Czasami odwracanie wierszy i kolumn kontenera nie wystarcza. W takich przypadkach, moemy zastosowa wasno <code>order</code> do pojedynczego elementu. Domylnie elementy maj warto 0, ale przy pomocy tej wasnoci moemy ustali kolejno na dowoln dodatni lub ujemn warto.</p><p>Przy pomocy wasnoci <code>order</code> zmie kolejno abek tak, aby kada bya na swoim listku.</p>',
          'cs': '<p>Samotn zmna smru nkdy nesta. V tchto ppadech lze zvolit poad pomoc vlastnosti <code>order</code> jednotlivch prvk. Vchoz hodnota je 0, ale mete pout libovoln kladn i zporn cel slo.</p><p>Pouijte vlastnost <code>order</code> tak, aby se abky ocitly na sprvnch leknnech.</p>',
          'ja': '<p><code>order</code>0</p><p><code>order</code></p>',
          'hu': '<p>Idnknt a sorok vagy az oszlopok irnynak megfordtsa nem elgsges. Ilyen esetekben alkalmazhatjuk az <code>order</code> tulajdonsgot az egyes elemekre. Alaprtelmezettknt az elemek a 0 rtket kapjk, de lehetsgnk van negatv vagy pozitv egsz szmot megadni rtkknt.</p><p>Hasznld az <code>order</code> tulajdonsgot, hogy trendezd a bkkat a tndrrzsiknak megfelelen.</p>',
          'eo': '<p>Kelkfoje, inversante la vican a kolumnan ordonon de ujo ne sufias. Tiaokaze, ni povas apliki la <code>order</code> trajton al individuaj elementoj. Defalte, elemento havas nulan valoron, sed oni povas uzi i tiun trajton por in igi pozitiva a negativa entjera valoro.</p><p>Uzu la <code>order</code> trajton por reordigi la ranojn la iliaj nimfeofolioj.</p>',
          'sv': '<p>Ibland rcker det inte med att ndra ordningen p en rad eller kolumn. I s fall kan man lgga till <code>order</code> p ett individuellt objekt. Frn brjan har alla objekt vrdet 0, men vi kan anvnda denna attribut till att stta ett positivt eller negativt heltalsvrde.</p><p>Anvnd <code>order</code> till att ndra ordningen p grodorna s att de hamnar p rtt blomblad.</p>',
          'uk': '<p>       .        <code>order</code>   .       0,           .</p><p>  <code>order</code>       .</p>',
          'hi': '<p>-                <code>order</code>                             </p><p><code>order</code>         </p>',
          'sr': '<p>          .      <code>order</code>    . ,    0,               .</p><p> <code>order</code>         .</p>',
          'nl': '<p>Soms is het omkeren van de rij/kolom volgorde van een container niet genoeg. In zulke gevallen kunnen we gebruik maken van de <code>order</code> eigenschap op individuele elementen. De standaardwaarde voor elementen is 0, maar we kunnen dit op een positief of negatief geheel getal instellen.</p><p>Maak gebruik van de <code>order</code> eigenschap om de kikkers volgens de volgorde van de lelieblaadjes te ordenen.</p>',
          'ta': '<p>  (row)  (column)      .    (items)  <code>order</code> property . ,  0   ,   <code>order</code> property   (positive)  )negative)   .</p><p><code>order</code> property        </p>',
