          'it': '<p>Aiuta questo gruppo di rane a disporsi in tre colonne ordinate usando una combinazione di <code>flex-direction</code> e <code>flex-wrap</code>.</p>',
          'ko': '<p><code>flex-direction</code> <code>flex-wrap</code>    column    .</p>',
          'lt': '<p>Padkite iai varlyi armijai isirikiuoti trimis tvarkingais stulpeliais naudodami <code>flex-direction</code> ir <code>flex-wrap</code> komand kombinacij.</p>',
          'vi': '<p>Gip i qun ch xp thnh ba hng trt t bng cch kt hp <code>flex-direction</code> v <code>flex-wrap</code>.</p>',
          'pl': '<p>Pom tej zgrai abek utworzy trzy uporzdkowane kolumny uywajc wasnoci <code>flex-direction</code> oraz <code>flex-wrap</code>.</p>',
          'cs': '<p>Pomozte abkm utvoiy ti zstupy pomoc <code>flex-direction</code> a <code>flex-wrap</code>.</p>',
          'ja': '<p><code>flex-direction</code><code>flex-wrap</code></p>',
          'hu': '<p>Segts ezt a bkahadsereget, hogy hrom szablyos oszlop alakzatba rendezdjenek a <code>flex-direction</code> s a <code>flex-wrap</code> hasznlatnak tvzsvel.</p>',
          'eo': '<p>Helpu tiun i aron da ranoj formi tri ordigotajn kolumojn uzante kombinon de <code>flex-direction</code> kaj <code>flex-wrap</code>.</p>',
          'sv': '<p>Hjlp en hel drs av grodor att stlla sig i tre raka kolumner genom att anvnda en kombination av <code>flex-direction</code> och <code>flex-wrap</code>.</p>',
          'uk': '<p>      ,     <code>flex-direction</code>  <code>flex-wrap</code>.</p>',
