          'sr': '<p>          <code>flex-direction</code>  <code>flex-wrap</code>.</p>',
          'nl': '<p>Help dit legertje kikkers om drie ordelijke kolommen te vormen, maak hiervoor gebruik van een combinatie van <code>flex-direction</code> en <code>flex-wrap</code>.</p>',
          'ta': '<p><code>flex-direction</code>  <code>flex-wrap</code>       .</p>',
