          'hu': '<p>Vidd haza a bkkat mg egyszer utoljra azon CSS tulajdonsgok segtsgvel, amelyeket eddig tanultl:</p><ul><li><code>justify-content</code></li><li><code>align-items</code></li><li><code>flex-direction</code></li><li><code>order</code></li><li><code>align-self</code></li><li><code>flex-wrap</code></li><li><code>flex-flow</code></li><li><code>align-content</code></li></ul>',
          'eo': '<p>Revenigu la ranojn hejmen por lasta fojo uzante la CSS-ajn trajtojn kiujn vi lernis:</p><ul><li><code>justify-content</code></li><li><code>align-items</code></li><li><code>flex-direction</code></li><li><code>order</code></li><li><code>align-self</code></li><li><code>flex-wrap</code></li><li><code>flex-flow</code></li><li><code>align-content</code></li></ul>',
