          'sr': '<p>        CSS    /:</p><ul><li><code>justify-content</code></li><li><code>align-items</code></li><li><code>flex-direction</code></li><li><code>order</code></li><li><code>align-self</code></li><li><code>flex-wrap</code></li><li><code>flex-flow</code></li><li><code>align-content</code><img src="http://code.org/api/hour/finish_flexbox_froggy.png"></li></ul>',
