        'pt-br': '<p>Voc venceu! Graas  sua maestria de flexbox, voc foi capaz de ajudar todos os sapos a chegar at suas vitrias-rgias. Veja como eles esto felizes!</p><p>Se voc achou isso coaxante, confira meus outros projetos no meu <a href="http://thomaspark.co">blog</a> ou <a href="http://twitter.com/thomashpark">Twitter</a>, e compartilhe Flexbox Froggy com seus amigos!</p>',
        'es': '<p>T ganas! gracias a tu dominio de flexbox, fuiste capaz de ayudar a todas las ranas a ir a sus hojas de lirio. Basta mirar lo felices que son!</p><p>Si te pareci divertido, revisa otros proyectos en mi <a href="http://thomaspark.co">blog</a> o <a href="http://twitter.com/thomashpark">Twitter</a> y asegurate de compartir Flexbox Froggy con tus amigos.</p>',
