        'ru': '<p> !     flexbox,         .  ,   !</p><p>  ,       <a href="http://thomaspark.co"></a>  <a href="http://twitter.com/thomashpark"></a>,         !</p>',
        'fa': '<p>  !             .           !</p><p>       <a href="http://thomaspark.co"></a>  <a href="http://twitter.com/thomashpark"></a>   ,                !</p>',
        'zh-cn': '<p>!flexbox</p><p><a href="http://thomaspark.co"></a><a href="http://twitter.com/thomashpark">Twitter</a>Flexbox Froggy!</p>',
        'zh-tw': '<p>flexbox</p><p><a href="http://thomaspark.co"></a><a href="http://twitter.com/thomashpark">Twitter</a>Flexbox Froggy!</p>',
        'tr': '<p>Kazandnz! Flexbox ustalnz sayesinde btn kurbaalar nilfer yapraklarna gtrebildik. Bakn, nekadar da mutlular!</p><p>Eer bunu beendiyseniz, dier projelerime <a href="http://thomaspark.co">blog</a> ya da <a href="http://twitter.com/thomashpark">Twitter</a> adreslerinden gz gezdirin, ve Flexbox Froggy oyununu arkadalarnzla paylan!</p>',
        'it': '<p>Hai vinto! Grazie alla tua conoscenza di flexbox sei riuscito ad aiutare tutte le rane a trovare la loro ninfea. Guarda come sono felici ora!</p><p>Se hai trovato questo gioco interessante, visita il mio <a href="http://thomaspark.co">blog</a> o il profilo  <a href="http://twitter.com/thomashpark">Twitter</a> per aggiornamenti sugli altri miei progetti. Non dimenticare di condividere Flexbox Froggy con i tuoi amici!</p>',
        'ko': '<p>!   Flexbox           . Froggy   !</p><p>        : <a href="http://thomaspark.co">blog</a> or <a href="http://twitter.com/thomashpark">Twitter</a>   Flexbox Froggy    !</p>',
        'lt': '<p>Js laimjote! Js valdyto "flexbox" metodo dka, skmingai pagelbjote visoms varlytems pasiekti savo lelijos lapus. Tik pairkite kaip jos okinja i diaugsmo!</p><p>Jei manote, kad ios pamokos Jums buvo naudingos, umeskite ak  kitus mano projektus mano <a href="http://thomaspark.co">dienoratyje</a> ar <a href="http://twitter.com/thomashpark">Twitter</a> paskyroje ir btinai pasidalinkite "Flexbox Froggy" su draugais!</p>',
        'vi': '<p>Chin thng! Nh s thnh tho flexbox ca bn, bn  gip tt c cc ch ch tr v vi nhng l bng sng ca chng. Nhn xem chng hnh phc bit bao nhiu!</p><p>Nu bn thy d n ny li cun, hy xem cc d n khc ca ti trn <a href="http://thomaspark.co">blog</a> hoc l <a href="http://twitter.com/thomashpark">Twitter</a>, v hy chia s Flexbox Froggy vi bn b!</p>',
        'pl': '<p>Wygrae! Dziki twemu mistrzostwu w obsudze flexboxa moge pomc wszystkim abkom dotrze do swoich listkw. Popatrz tylko jakie s szczliwe!</p><p>Jeli spodobaa ci si gra obczaj inne moje projekty na moim <a href="http://thomaspark.co">blogu</a> lub koncie <a href="http://twitter.com/thomashpark">Twittera</a>, a take podziel si Flexbox Froggy z przyjacimi!</p>',
        'cs': '<p>Vyhrli jste! Svm mistrovskm pouitm flexboxu jste pomohli vem abkm najt sv leknny. Podvejte, jak jsou astn!</p><p>Jestli se vm hra lbila, podvejte se i na m dal projekty na mm <a href="http://thomaspark.co">blogu</a> nebo na <a href="http://twitter.com/thomashpark">Twitteru</a>, a podlte se o radost z Flexbox Froggy s kamardy!</p>',
        'ja': '<p>flexbox</p><p><a href="http://thomaspark.co"></a><a href="http://twitter.com/thomashpark">Twitter</a>Flexbox Froggy</p>',
        'hu': '<p>Nyertl! A flexbox szakrtelmednek ksznheten minden bkt sikeresen hazajuttattl a tndrrzsjra. Nzd csak, hogy ugrlnak rmkben!</p><p>Ha eljutottl eddig a brekegsig, nzz r tovbbi munkimra a <a href="http://thomaspark.co">blogomon</a> vagy a <a href="http://twitter.com/thomashpark">Twitteren</a>, s felttlenl oszd meg a Flexbox Froggy-t a bartaiddal!</p>',
        'eo': '<p>Vi gajnas! Dank\'al via majstreco de flexbox, vi ja kapablis helpi iujn ranojn reveni al iliaj akvolilifolioj. Rigardu kiel ili saltas pro ojeco!</p><p>Se vi trovis amuzanta tiun ludon, bonvolu rigardi aliajn projektojn miajn je mia <a href="http://thomaspark.co">blogo</a> a <a href="http://twitter.com/thomashpark">Twitter</a>, kaj bonvolu disdoni Flexbox Froggy-on al tutaj amikoj viaj!</p>',
        'sv': '<p>Grattis, du vann! Tack vare dina mstarkunskaper kring flexbox lyckades du hjlpa alla grodorna till sina blomblad. Se bara hur glada dom r!</p><p>Om du tyckte detta var spnnande, spana in mina vriga projekt p min <a href="http://thomaspark.co">blogg</a> eller <a href="http://twitter.com/thomashpark">Twitter</a> och dela grna Flexbox Froggy med dina vnner!</p>',
        'uk': '<p> !     flexbox,          .     !</p><p>    ,      <a href="http://thomaspark.co"></a>  <a href="http://twitter.com/thomashpark"></a>,      Flexbox Froggy   !</p>',
        'hi': '<p>  !  flexbox      ,                     </p><p>             <a href="http://thomaspark.co">blog</a>  <a href="http://twitter.com/thomashpark">Twitter</a>              ,     flexbox     </p>',
        'sr': '<p>/ !    , /         .     !</p><p>  /,      <a href="http://thomaspark.co"></a>  <a href="http://twitter.com/thomashpark"></a>    Flexbox Froggy   !</p>',
        'nl': '<p>Gewonnen! Dankzij je beheersing van flexbox was je in staat om alle kikkers te helpen hun lelieblaadjes te bereiken. Kijk eens hoe gelukkig ze zijn!</p><p>Indien je dit al leuk vond, geef dan eens een kijkje naar mijn andere projecten op mijn <a href="http://thomaspark.co">blog</a> of <a href="http://twitter.com/thomashpark">Twitter</a>, en vergeet niet Flexbox Froggy te delen met je vrienden!</p>',
        'ta': '<p>! !! Flexbox-    ,          .  .      !',
        'ml': '<p> !        . !</p><p>  ,    <a href="http://cssgridgarden.com/">Grid Garden</a>   <a href="http://thomaspark.co">my blog</a> or <a href="http://twitter.com/thomashpark">Twitter</a>.</p><p>Flexbox Froggy-     ?        <a href="http://treehouse.7eer.net/c/371033/228915/3944?subId1=flexboxfroggy">Treehouse</a>.   Flexbox Froggy  !</p>',
