          'ru': '<p>    grid      ,    <code>grid-column-start</code>  <code>grid-column-end</code>  . ,      -1   1- grid   .</p><p>  <code>grid-column-end</code>  .</p>',
          'pl': '<p>Jeeli chcesz liczy linie od prawej zamiast od lewej, moesz ustawi wartoci <code>grid-column-start</code> i <code>grid-column-end</code> na negatywne. Na przykad, moesz ustawi warto na -1 aby wybra pierwsz lini od prawej.</p><p>Sprbuj ustawi <code>grid-column-end</code> na negatywn warto.</p>',
          'es': '<p>Si prefieres contar las lneas de la cuadrcula comenzando por la derecha, puedes dar a <code>grid-column-start</code> y <code>grid-column-end</code> valores negativos. Por ejemplo, puedes establecerlos a -1 para indicar la primera lnea comenzando por la derecha.</p><p>Intenta establecer <code>grid-column-end</code> a un valor negativo.</p>',
          'sv': '<p>Ifall du vill rkna gridlinjer frn hger istllet fr vnster kan du anvnda negativa vrden fr <code>grid-column-start</code> och <code>grid-column-end</code>.</p><p>Frsk stta <code>grid-column-end</code> till ett negativt vrde.',
          'fa': '<p>           ,     <code>grid-column-start</code>  <code>grid-column-end</code>   .  ,             1- .</p><p>   <code>grid-column-end</code>    .</p>',
