          'nl': '<p>Inplaats van een grid item te definiren op basis van de start en eind posities van de grid lijnen, kan je hem definiren op basis van het aantal colommen dat je wilt invullen, door het <code>span</code> sleutelwoord te gebruiken. Onthoud dat <code>span</code> alleen werkt met positieve waardes.</p><p>Bijvoorbeeld, geef deze planten water met de regel  <code>grid-column-end: span 2;</code>.</p>',
          'pt-br': '<p>Ao invs de definir uma grade baseada no incio e final das posies das linhas na grade, voc pode defini-las baseado na largura da coluna desejada usando a palavra-chave <code>span</code>. Mantenha em mente que <code>span</code> apenas funciona com valores positivos.</p><p>Por exemplo, irrigue essas cenouras com a regra <code>grid-column-end: span 2;</code>.</p>',
          'ru': '<p>  grid        grid ,          ,  <code>span</code>. ,  <code>span</code>     .</p><p> ,      <code>grid-column-end: span 2;</code>.</p>',
          'pl': '<p>Zamiast definiowa element na podstawie pocztkowej i kocowej linii, moesz go zdefiniowa na podstawie podanej szerokoci z uyciem sowa kluczowego <code>span</code>. Miej na uwadze, e <code>span</code> dziaa tylko z pozytywnymi wartociami.</p><p>Na przykad, podlej te marchewki poleceniem <code>grid-column-end: span 2;</code>.</p>',
          'es': '<p>En lugar de definir un elemento en la cuadrcula basado en la posicion inicial y final, puedes definirlo basado en la longitud de columnas deseada usando la palabra clave <code>span</code>. Ten presente que <code>span</code> solo funciona con valores positivos.</p><p>Por ejemplo, riega las zanahorias usando <code>grid-column-end: span 2;</code>.</p>',
          'sv': '<p>Istllet fr att definiera ett grid element baserat p start- och slutvrden kan man definiera det baserat p nskad kolumnbredd genom att stta <code>span</code>. Kom ihg att <code>span</code> dock bara fungerar med positiva vrden.</p><p>Till exempel, vattna dom hr mortterna med <code>grid-column-end: span 2;</code>.</p>',
