          'ko': '<p><code>grid-template-rows</code> <code>grid-template-columns</code>   .</p><p><code>grid-template-rows</code>    50px   .  5 (row) , 5 (row) .</p>',
          'el': '<p> <code>grid-template-rows</code>     <code>grid-template-columns</code>.</p><p>  <code>grid-template-rows</code>         50 pixels   .           5  ,      5  .</p>',
