          'es': '<p>Ayuda a las ranas a encontrar sus hojas de lirio usando <code>flex-direction</code> y <code>justify-content</code>.</p><p>Nota que cuando es una columna, <code>justify-content</code> cambia a vertical y <code>align-items</code> a horizontal.</p>',
          'fr': '<p>Aidez les grenouilles  trouver leurs nnuphars en utilisant <code>flex-direction</code> et <code>justify-content</code>.</p><p>Remarquez que quand la direction est en colonne, <code>justify-content</code> change pour la verticale et <code>align-items</code> pour l\'horizontale.</p>',
          'ru': '<p>       <code>flex-direction</code>  <code>justify-content</code>.</p><p>,      ,  <code>justify-content</code>    ,  <code>align-items</code>  .</p>',
          'fa': '<p>                  <code>flex-direction</code>  <code>justify-content</code>  .</p><p>    <span class="en-font">flex-direction</span>  <span class="en-font">column</span>  ,  <code>justify-content</code>   <code>align-items</code>   .</p>',
          'zh-cn': '<p><code>flex-direction</code><code>justify-content</code></p><p>flex<code>justify-content</code><code>align-items</code></p>',
          'zh-tw': '<p><code>flex-direction</code><code>justify-content</code></p><p>flex, <code>justify-content</code>, <code>align-items</code></p>',
          'tr': '<p><code>flex-direction</code> ve <code>justify-content</code> kurallarn kullanarak kurbaalarn kendi nilfer yapraklarn bulmalarna yardmc olun.</p><p>flex direction kural column deerini aldnda, <code>justify-content</code> kural dikeyde ve <code>align-items</code> kural yatayda alr, unutmayn.</p>',
          'it': '<p>Aiuta le rane a raggiungere le ninfee utilizzando <code>flex-direction</code> e <code>justify-content</code>.</p><p>Nota che quando la direzione flex e\' una colonna,  <code>justify-content</code> cambia l\'allineamento verticale e <code>align-items</code> quello orizzontale.</p>',
          'ko': '<p>         . <code>flex-direction</code> <code>justify-content</code>   .</p><p>Flex  column  <code>justify-content</code>  , <code>align-items</code>   .</p>',
