          'hu': '<p>Jajj, ne! A bkk mind rprseldtek egyetlen sor tndrrzsra. Oszlasd el ket a <code>flex-wrap</code> tulajdonsg hasznlatval, amely a kvetkez rtkeket veheti fel:</p><ul><li><code>nowrap</code>: Minden elem egyetlen sorba tmrdik.</li><li><code>wrap</code>: Az elemek tovbbi sorokba trnek.</li><li><code>wrap-reverse</code>: Az elemek tovbbi sorokba trnek fordtott irnyban.</li></ul>',
