          'sv': '<p>h nej! Alla grodorna r ihopklmnda p en och samma rad. Sprid ut dom genom att anvnda <code>flex-wrap</code>, vilket accepterar fljande vrden:</p><ul><li><code>nowrap</code>: Alla objekt r placerade p en enkel rad.</li><li><code>wrap</code>: Objekten spiller ver till flera rader.</li><li><code>wrap-reverse</code>: Objekten splller ver till flera rader i omvnd ordning.</li></ul>',
